}

#wrapper-register a:hover {
	color:#ff2a00;
	font-weight: bold;
}

/* === Sign in Form === */
#register {
	height: 90px;
	width: 300px;
	border-radius: 8px;
	position: relative;
}
#register::before {
	display: block;
	position: relative;
	height: 2px;
	background: rgb(52, 56, 61);
	content: '';
	top: 44px;
	margin-left: 20px;
	z-index: 1;
}
#register input:first-of-type{
	border-top-right-radius: 8px;
	border-top-left-radius: 8px;
}
#register input:last-of-type{
	border-bottom-right-radius: 8px;
	border-bottom-left-radius: 8px;
}
#register  input[type="text"], #register  input[type="password"], #register .submit{
	background: rgb(28, 30, 33);
	box-shadow: inset -100px -100px 0 rgb(28, 30, 33); /*Prevent yellow autofill color*/
	color: rgb(52, 56, 61);
}
#register  input[type="text"], #register  input[type="password"]{
	position: relative;
	display: block;
	width: 280px;
	height: 45px;
	border: 0;
	outline: 0;
	top: -2px;
	padding: 0 0 0 20px;
	font-weight: 700;
}
#register  input[type="text"]:focus, #register  input[type="password"]:focus{
	color: rgb(255, 255, 255);
}
#register .submit{
  display: block;
  position: absolute;
  width: 30px;
  height: 35px;
  color: rgb(52, 56, 61);
  border-radius: 100%;
  outline: 0;
  z-index: 2;
  top: 19px;
  right: -24px;
  border: 6px solid rgb(52, 56, 61);
  font-size: 25px;
  text-indent: 0px;
  padding-left: 6px;
  padding-bottom: 1px;
  text-align: center;
}
#register .submit:hover{
	color: #ed5739;
	text-shadow: 0 0 10px #ed5739;
	cursor: pointer;
}
#register p {
	color: rgb(79, 85, 97);
	padding: 0 20px;
	font-weight: 700;
	font-size: 12px;
	margin: 5px 0 0 0;
}
#register p > a{
	color: rgb(111, 119, 135);
	text-decoration: none;
}
#register p > a:hover{
	border-bottom: 1px solid;
}

#register-succesful{
	position: absolute;
	left: 50%;
	margin-left: -200px;
	top: 50%;
	margin-top: -80px;
	width: 370px;
	height: 160px;
	border: 1px solid #ed5739;
	border-radius: 20px;
}

#register-succesful h1{
	position: relative;
	top: 35px;
	color: #ed5739;
	width: 100%;
}
